import { newE2EPage } from '@stencil/core/testing';

describe('presto-youtube', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-youtube></presto-youtube>');

    const element = await page.find('presto-youtube');
    expect(element).toHaveClass('hydrated');
  });
});
